import React from "react";
import styles from "./login.module.css";

function LoginElement({ type, label }) {
  return (
    <label>
      <span>{label}: </span>
      <input type={type} />
    </label>
  );
}

export default function Login() {
  return (
    <section className={styles.login}>
      <h1>Apex Rewards</h1>
      <form action="/transactions" method="POST">
        <LoginElement type="email" label="Email" />
        <LoginElement type="password" label="Password" />
        <button>Track my Rewards!</button>
      </form>
    </section>
  );
}
